<template>
  <div id="list" v-if="isShow">
    <div class="list1">
      <div class="list1-con1">
        <a-button type="primary" class="btn"> 全部商品(4000) </a-button>
        <a-button type="primary" class="btn"> 已上架(1000) </a-button>
        <a-button type="primary" class="btn"> 未上架(1000) </a-button>
      </div>
      <div class="list1-con2">
        <span class="iconfont iconiconxiazai"></span>
        <h1 @click="refreshAction">刷新</h1>
      </div>
    </div>

    <div class="list2">
      <div class="list2-top">
        <span>商品筛选</span>
        <div @click="packAction">
          <h2>收起</h2>
          <span class="iconfont iconjiantou"></span>
        </div>
      </div>
      <div v-if="isPre">
        <ul class="list2-con1">
          <li>
            <span>关键词:</span>
            <input
              type="text"
              value=""
              class="input"
              placeholder="商品名称/商品货号"
            />
          </li>
          <li>
            <span>分类:</span>
            <select name="" id="" class="select" v-model="selected">
              <option v-for="(item, index) in options" :key="index">
                {{ item.value }}
              </option>
              <!-- <button>筛选</button> -->
            </select>
          </li>
        </ul>

        <div class="list2-con2">
          <button class="btn" @click="resetAction">重置</button>
          <button class="btn" @click="screenAction(selected)">筛选</button>
        </div>
      </div>
    </div>

    <div class="list3">
      <h2>商品列表</h2>
      <table>
        <tr>
          <th><input type="checkbox" /></th>
          <th>序号</th>
          <th>商品名称</th>
          <th>图片</th>
          <th>价格</th>
          <th>分类</th>
          <th>销量</th>
          <th>操作</th>
        </tr>

        <tr v-for="item in dataPart" :key="item._id">
          <td><input type="checkbox" /></td>
          <td>{{ item.goodsId }}</td>
          <td>{{ item.goodsName }}</td>
          <td><img :src="item.goodsImage" alt="" /></td>
          <td>{{ item.goodsPrice }}</td>
          <td>{{ item.goodsCategory }}</td>
          <td>{{ item.goodsSales }}</td>
          <td>
            <span @clic="editAction">编辑</span>
            <h1 @click="deleteAction(item._id)">删除</h1>
          </td>
        </tr>
      </table>

      <div class="list3-con1">
        <h1>共{{ pages }}页</h1>
        <h3>{{ dataAll.length }}条数据</h3>
        <span class="iconfont iconarrow-down1" @click="pre"></span>

        <div
          v-for="(item, index) in pages"
          :key="index"
          class="list3_num"
          @click="changeIndex(index)"
        >
          <h4>{{ index + 1 }}</h4>
          <!-- <h4>{{ pages }}</h4> -->
        </div>
        <span class="iconfont iconarrow-down2" @click="next"></span>
      </div>

      <ul>
        <li v-for="item in dataCate" :key="item.id">
          {{ item.goodsName }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { Button, Input, Menu, Icon, Dropdown } from "ant-design-vue";
import { mapState } from "vuex";
export default {
  data() {
    return {
      showIndex: 0,
      pageIndex: 0,
      darapart: 10,
      isPre: true,
      isShow: false,
      selected: "新品专区",
      options: [
        { value: "推荐专区" },
        { value: "新品专区" },
        { value: "居家生活" },
        { value: "服饰鞋包" },
        { value: "美食酒水" },
        { value: "个人清洁" },
        { value: "母婴亲子" },
        { value: "运动旅行" },
        { value: "数码家电" },
      ],
    };
  },
  components: {
    [Button.name]: Button,
    [Input.name]: Input,
    [Icon.name]: Icon,
    [Dropdown.name]: Dropdown,
    [Menu.name]: Menu,
    [Menu.Item.name]: Menu.Item,
  },
  watch: {
    pageIndex() {
      this.$store.dispatch("goods/requestGoodsInfo", this.pageIndex);
    },
  },
  computed: {
    pages() {
      return Math.ceil(this.dataAll.length / this.darapart);
    },
    ...mapState({
      dataAll: (state) => state.goods.goodsAll,
      dataPart: (state) => state.goods.goodsPart,
      dataCate: (state) => state.goods.goodsCate,
      // data:state=>{console.log(state.goods.goodslist)},
    }),
  },
  methods: {
    changeIndex(index) {
      console.log(index);
      this.pageIndex = index;
    },
    packAction() {
      this.isPre = !this.isPre;
    },
    editAction() {},
    // 进行物品的筛选
    screenAction(value) {
      this.$store.dispatch("goods/requireCategoryInfo", value);
    },
    resetAction() {
      // this.isShow = true;
      this.$store.dispatch("goods/requestGoodsInfo", this.pageIndex);
    },
    deleteAction(id) {
      this.$store.dispatch("goods/requestDelete", id);
      this.$store.dispatch("goods/requestGoodsInfo", this.pageIndex);
    },
    pre() {
      this.pageIndex--;
      if (this.pageIndex < 0) {
        this.pageIndex = 0;
      }
    },
    next() {
      this.pageIndex++;
      if (this.pageIndex > this.pages - 1) {
        this.pageIndex = this.pages - 1;
      }
    },
    refreshAction() {
      this.$store.dispatch("goods/requestGoodsInfo", this.pageIndex);
    },
  },
  created() {
    this.isShow = true;
    this.$store.dispatch("goods/requestGoodsInfo", this.pageIndex);
  },
};
</script>

<style scoped lang="scss">
#list {
  .list1 {
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .list1-con1 {
      .btn {
        margin-right: 10px;
      }
    }
    .list1-con2 {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      width: 60px;
      height: 24px;
      line-height: 24px;
      background-color: #f2f2f2;
      border-radius: 4px;
      h1 {
        margin-left: 6px;
        font-size: 12px;
        cursor: pointer;
        margin-top: 3px;
      }
    }
  }

  .list2 {
    margin-top: 15px;
    .list2-top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 30px;
      background-color: #f2f2f2;
      padding: 0 20px;
      font-weight: 700;
      div {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        h2 {
          margin: 0px 4px 0px 4px;
        }
      }
    }
    .list2-con1 {
      display: flex;
      height: 40px;
      justify-content: flex-start;
      align-items: center;
      color: #333;
      margin: 6px 0px;
      li {
        width: 30%;
        padding: 0 20px;
        span {
          font-size: 14px;
          font-weight: 700;
          margin-right: 10px;
        }
        .input {
          height: 24px;
          border-radius: 4px;
          font-size: 8px;
          color: #999;
          padding: 2px 4px;
          border: 1px solid #999;
        }
        .select {
          width: 50%;
          border-radius: 4px;
          font-size: 8px;
          color: #999;
          padding: 2px 4px;
          border: 1px solid #999;
        }
      }
    }
    .list2-con2 {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 30px;
      margin-top: 10px;
      .btn {
        width: 60px;
        background-color: #ccc;
        color: #333;
        margin: 0 6px;
        font-size: 12px;
        font-weight: 700;
        border: 2px solid #999;
      }
    }
  }
  .list3 {
    width: 100%;
    min-height: 300px;
    h2 {
      margin-top: 20px;
      padding-bottom: 10px;
      font-weight: 700;
      padding-left: 20px;
      height: 30px;
      line-height: 30px;
      background-color: #f2f2f2;
    }
    table {
      width: 100%;
      text-align: center;

      tr {
        height: 30px;
        line-height: 30px;

        th {
          border: 1px solid #ccc;
          box-sizing: border-box;
        }
        td {
          border: 1px solid #ccc;
          box-sizing: border-box;
        }
        td:last-child {
          width: 100px;
          text-align: center;
          font-size: 12px;

          span {
            float: left;
            padding-left: 12px;
            color: blue;
            cursor: pointer;
          }
          h1 {
            color: blue;
            cursor: pointer;
          }
        }
        td:nth-child(4) {
          img {
            width: 20px;
            height: 20px;
          }
        }
      }
    }
    .list3-con1 {
      height: 30px;
      display: flex;
      justify-content: flex-start;
      line-height: 30px;
      background-color: #f2f2f2;
      align-items: center;
      margin-top: 10px;
      h1 {
        padding-left: 20px;
        margin-right: 10px;
      }
      h1,
      h3 {
        font-size: 14px;
        font-weight: 700;
        margin-top: 3px;
      }
      h3 {
        margin-right: 20px;
      }
      .list3_num {
        h4 {
          box-sizing: border-box;
          line-height: 20px;
          width: 20px;
          height: 20px;
          background: #fff;
          padding: 1px 4px 2px;
          margin: 0 5px;
        }
      }
    }
  }
}
</style>